@import url('https://fonts.googleapis.com/css2?family=Public+Sans:wght@200&family=Quicksand:wght@400;500&display=swap');
*{
  margin: 0;
  font-family: 'Quicksand';
  color: blueviolet;
 
}
.navbar{
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 650px;
  margin: 0 auto;
}
.navbar h1{
  color: tomato;
}
.navbar .links{
  margin-left: auto;
}
.navbar a{
  text-decoration: none;
  padding: 6px;
  margin-left: 16px;
}
.navbar a:hover{
  color:crimson;
  font-weight: bolder;
}
.content{
  max-width: 600px;
  padding: 16px;
  margin: 40px ;
}
.blog-preview{
  padding: 10px 16px;
  margin: 20px;
  border-bottom: 1px solid white;
}
.blog-preview:hover{
  box-shadow: 1px 2px 5px rgba(0,0,0,0.1);
}
.blog-preview h2{
  color: red;
  font-size: 20px;
  margin-bottom: 0px;
}
